exports.install = function (Vue, options) {
    /**
     * 
     * @param {*} time1 对比日期，此为必选参数
     * @param {*} time2 被对比日期，此为可选项，不填则表示是否显示当前日期
     */
    Vue.prototype.showDate = function (time1 = '', time2 = '') {
        console.log('Time1:' + time1);
        // 实例化当前的时间对象
        var nowObj = new Date();
        // 判断是否为两个时间的比较
        if (time1 && time2) {
            console.log("<time1&time2>");
            console.log('time1:' + time1 + '; time2:' + time2);
            console.log("</time1&time2>");
            // 实例化上一条对比消息的时间对象
            var DateObj1 = new Date(Number(time1) * 1000);
            // 实例化需要显示的时间对象
            var DateObj2 = new Date(Number(time2) * 1000);
            // 判断是否需要显示时间（即与上一条消息的时间间隔是否大于30分钟）
            if ((DateObj2.getTime() - DateObj1.getTime()) >= 30 * 60) {
                // 判断时间戳之差是否小于24小时
                if ((nowObj.getTime() - DateObj2.getTime()) <= 24 * 60 * 60) {
                    // 判断日期是否相同（时间戳差值小于24小时，但是日期不同即为昨天，否则为当天)
                    if (DateObj2.toLocaleDateString() != nowObj.toLocaleDateString()) {
                        // 返回昨天拼接上时间
                        return '昨天 ' + DateObj2.toLocaleTimeString();
                    } else {
                        // 返回时间
                        return DateObj2.toLocaleTimeString();
                    }
                } else {
                    // 返回完整的日期时间
                    return DateObj2.toLocaleString();
                }
            } else {
                return false;
            }
        } else if (time1 && !time2) {
            console.log("<time1>");
            console.log(time1);
            console.log("</time1>");
            // 实例化上一条对比消息的时间对象
            var DateObj1 = new Date(Number(time1) * 1000);
            // 判断当前消息是否显示时间（即与上一条消息的时间间隔是否大于30分钟）
            if ((nowObj.getTime() - DateObj1.getTime()) >= 30 * 60) {
                // 返回时间
                return nowObj.toLocaleTimeString();
            } else {
                return false;
            }
        } else if (!time1 && time2) {
            console.log("<time2>");
            console.log("string:" + time2 + "; number:" + Number(time2))
            console.log("</time2>");
            var DateObj2 = new Date(Number(time2) * 1000);
            var time = nowObj.getTime() - DateObj2.getTime();
            // 判断是否为今天或者昨天的消息
            if (time <= 24 * 60 * 60) {
                // 判断是否显示（即时间差大于等于30分钟）
                if (time >= 30 * 60) {
                    // 判断是否为今天的消息
                    if (nowObj.toLocaleDateString() == DateObj2.toLocaleDateString()) {
                        return DateObj2.toLocaleTimeString();
                    } else {
                        return '昨天 ' + DateObj2.toLocaleTimeString();
                    }
                } else {
                    return false;
                }
            } else {
                // 返回日期时间
                return DateObj2.toLocaleString();
            }
        } else {
            console.log('nothing...');
            return false;
        }
    }
    /**
     *  动态添加属性
     * @param {*} obj 添加属性的对象 
     * @param {*} data 数组或者对象
     */
    Vue.prototype.setGlobalAttribute = function (obj, data) {
        for (var index in data) {
            var type = Object.prototype.toString.call(data[index]);
            if (type == '[object Object]' || type == '[object Array]') {
                this.$set(obj, index, {});
                this.setGlobalAttribute(obj[index], data[index]);
            } else {
                this.$set(obj, index, data[index]);
            }
        }
        // this.$forceUpdate();
    }
    /**
     *  websocket 发送消息
     * @param {*} data 一维键值对数组
     */
    Vue.prototype.senddata = function (data) {
        // console.log("sending Socket");
        if (this.GLOBAL.socketHand) {
            var text = '{';
            for (var index in data) {
                if (data[index]) {
                    if (typeof data[index] == 'number') {
                        text += '"' + index + '":' + data[index] + ',';
                    } else {
                        text += '"' + index + '":"' + data[index] + '",';
                    }
                }
            }
            text = text.substring(0, text.length - 1) + '}';
            this.GLOBAL.socketHand.send(text);
        }
    }
    /**
     * 关闭socket
     */
    Vue.prototype.closeSocket = function () {
        // console.log("closing Socket");
        if (this.GLOBAL.socketHand) {
            var data = {
                'send_id': this.GLOBAL.userInfo.id,
                'nickname': '',
                'get_id': '',
                'fname': '',
                'msg': 'I am leaving',
                'type': 'close',
                'rType': 1,
                'fType': 0
            };
            this.senddata(data);
            this.GLOBAL.socketHand.close();
            this.GLOBAL.socketHand = "";
        }
    }
    /**
     * 创建websocket
     */
    Vue.prototype.createSocket = function (callback) {
        console.log("creating socket...");
        // 创建websocket
        this.GLOBAL.socketHand = new WebSocket(this.GLOBAL.socketHost);
        var that = this;
        this.GLOBAL.socketHand.onopen = function (evt) {
            console.log("openning socket...");
            var data = {
                send_id: that.GLOBAL.userInfo.id,
                nickname: that.GLOBAL.userInfo.nickname,
                get_id: '',
                fname: '',
                msg: '',
                type: 'handle',
                rType: 1,
                fType: 0 // 标志是否为好友消息、陌生人消息，和user_message表的status对应
            }
            // 发送握手消息，告诉服务器本人id
            that.senddata(data);

        }
        // 监听websockt，接收消息
        this.GLOBAL.socketHand.onmessage = function (evt) {
            console.log("Recived Message: " + evt.data);
            if (evt.data) {
                var data = JSON.parse(evt.data);
                // console.log(data)
                switch (data.type) {
                    case "handle": //握手响应
                        handle(data, that);
                        break;
                    case "heart": //心跳响应
                        // 相应心跳信息
                        var data = {
                            send_id: that.GLOBAL.userInfo.id,
                            nickname: '',
                            get_id: '',
                            fname: '',
                            msg: 'I accept the heart',
                            type: 'heart',
                            rType: 1,
                            fType: 0 // 和user_message表的status对应，0表示没任何意义
                        }
                        that.senddata(data);
                        break;
                    default:

                        // 见接受到的数据保存到接受列表
                        that.GLOBAL.accept.push(data);
                        // 未读消息条数+1
                        that.GLOBAL.msgLength++;
                }
            }
            if(callback){
                callback(that);
            }
        }
    }

    /**
     * 握手处理函数
     * @param {*} data 
     * @param {*} that 
     */
    function handle(data, that) {
        if(data.send_id==-1){
            that.$messagebox.alert(data.msg);
            return ;
        }
        console.log("handling....");
        var sendList = that.GLOBAL.sendList;
        // 查看是否存在待发送的数据
        if (sendList.length > 0 && data.msg == 'ok') {
            for (var i = 0; i < sendList.length; i++) {
                that.senddata(sendList[i]);
            }
            // 发送完所有待发送的数据后，清空数据
            sendList = {};
        } else if (data.msg != 'ok') {
            that.$messagebox.alert(data.msg);
        }
        var msgList = data.userMsgs;
        console.log("<HANDLE>");
        console.log(msgList);
        console.log("</HANDLE>");

        // 保存未读消息条数
        for (var index in msgList) {
            if (index == 'system') {
                // 设置系统消息的条数
                that.$set(that.GLOBAL.friendMsgNum, index, msgList[index]);
                // 计算未读消息总条数
                that.GLOBAL.msgLength += msgList[index];
            } else {
                // 设置每个用户的未读消息条数
                that.$set(that.GLOBAL.friendMsgNum, index, msgList[index].length);
                // 计算未读消息总条数
                that.GLOBAL.msgLength += msgList[index].length;
            }
        }
        // 添加消息队列
        that.setGlobalAttribute(that.GLOBAL.msgList, msgList);
        // 添加朋友列表
        that.setGlobalAttribute(that.GLOBAL.friendLists, data.friendsList);
        // that.GLOBAL.friendLists.friendStatus = true;
        // 保存本地缓存
        that.$forceUpdate();
    }

}
console.log("BASE_JS");



