<template>
    <div>
        <!-- <mt-header :title="this.$route.query.val.nickname"> -->
        <mt-header v-bind:title="this.$route.query.nickname" fixed="true" style="height:10vw;font-size:5vw;">
            <router-link to="/friend" slot="left">
                <mt-button icon="back">返回</mt-button>
            </router-link>
            <router-link to="/addFriend" slot="right">
                <mt-button>
                    <i class="iconfont icon-yonghu" style="font-size:30px"></i>
                </mt-button>
            </router-link>
        </mt-header>
        <div class="pageContent chat">
            <div id="chat">
                <!-- <div class="list">
                    <div class="head">
                        <img src="./../../assets/pic1.png">
                    </div>
                    <div class="msgText">
                        <p>你叫什么名字？</p>
                        <div class="boo"></div>
                    </div>
                </div>
                <div class="list myList">
                    <div class="head">
                        <img v-bind:src="this.GLOBAL.Host + this.GLOBAL.userInfo.head">
                    </div>
                    <div class="msgText">
                        <p>我的名字叫做{{this.$route.query.nickname}}</p>
                        <div class="myboo"></div>
                    </div>
                </div> -->

                <div class="list" v-for="(item,index) in message" :key="index" :class="{myList: item.type}">
                    <p style="text-align:center;font-size:3vw;color:#999;margin-bottom:10px;" v-if="showchatRecordsDate(index)">{{showchatRecordsDate(index)}}</p>
                    <div style="display: flex;">
                        <div class="head">
                            <img v-bind:src="head">
                        </div>
                        <div class="msgText">
                            <p>{{item.content}}</p>
                            <div :class="item.type?('myboo'):('boo')"></div>
                        </div>
                    </div>
                </div>
            </div>

        </div>
        <div class="user">
            <div class="voiceBtn sendBox" style="width: 12vw;">
                <img src="../../assets/语音.svg" alt="">
            </div>
            <div class="content sendBox">
                <textarea name="" id="" cols="30" rows="10" v-model="myMsg"></textarea>
            </div>
            <div class="BTNBOX sendBox">
                <img src="../../assets/表情@2x.png" alt="">
                <button v-if="myMsg" @click="send">发送</button>
                <img v-else src="../../assets/chatadd.svg" alt="" style="margin-left:3vw;">
            </div>
        </div>
    </div>
</template>

<script>
console.log("COMMON_CHAT_VUE");
export default {
  data() {
    return {
      myMsg: "",
      message: this.GLOBAL.msgList[this.$route.query.fid],
      websock: null,
      nickname: this.$route.query.nickname,
      head: this.GLOBAL.Host + this.$route.query.head,
      that: this
    };
  },
  computed: {},
  methods: {
    showchatRecordsDate: function(index) {
      console.log("<index>");
      console.log(index);
      console.log("</index>");
      //   console.log("<message>");
      //   console.log(this.message);
      //   console.log("</message>");
      if (index - 1 >= 0) {
        console.log(this.message[index - 1].time);
        console.log(this.message[index].time);
        return this.showDate(
          this.message[index - 1].time,
          this.message[index].time
        );
      } else {
        console.log(this.message[index].time);
        return this.showDate("", this.message[index].time);
      }
    },
    send: function() {
      console.log(this.message);
      let msgtext = {
        send_id: this.GLOBAL.userInfo.id,
        nickname: this.GLOBAL.userInfo.nickname,
        get_id: "",
        fname: "",
        msg: "",
        type: "text",
        rType: 1,
        fType: this.$route.query.status // 标志是否为好友消息、陌生人消息，和user_message表的status对应
      };
      let text = {
        content: this.myMsg
      };
    }
  },
  mounted: function() {
    // let h = window.screen.height;
    // document.getElementById("chat").style.height = h - 137 + "px";
    // this.initWebSocket();
    // this.threadPoxi();
  },
  updated: function() {
    if (this.message != "") {
      this.$nextTick(() => {
        let lastChat = document.getElementById("chat").lastChild;
        if (lastChat) {
          lastChat.scrollIntoView();
        }
      });
    }
  },
  beforeCreate: function() {
    console.log("<userINfo>");
    console.log(this.GLOBAL.userInfo);
    console.log("</userINfo>");
    //   检测是否登陆
    if (!this.GLOBAL.userInfo.loginStatus) {
      this.$router.push("/login");
    }
    // console.log("<parma>");
    // console.log("fid:" + this.$route.query.fid);
    // console.log("nickname:" + this.$route.query.nickname);
    // console.log("</parma>");
    // 判断是否存在未读消息
    if (this.GLOBAL.friendMsgNum[this.$route.query.fid]) {
      console.log("changeStatus");
      // 判断是否创建socket
      if (!this.GLOBAL.socketHand) {
        this.createSocket();
      }
      //   发送修改消息阅读状态信号给服务器
      var data = {
        send_id: this.$route.query.fid,
        nickname: "",
        get_id: this.GLOBAL.userInfo.id,
        fname: "",
        msg: "change the readStatus",
        type: "readStatus",
        rType: 1,
        fType: this.$route.query.status
      };
      this.senddata(data);
    } else {
      console.log("no messages");
    }
    // 隐藏底部导航栏
    this.$parent.tabbarShow = false;
    // 清空未读消息条数
    this.GLOBAL.friendMsgNum[this.$route.query.fid] = 0;
    // this.$set(, this.$route.query.fid, 0);/
  }
};
</script>

<style scoped>
.user {
  height: 15vw;
  position: fixed;
  bottom: 0px;
  width: 100%;
  font-size: 5vw;
  background: white;
}
.user .sendBox {
  float: left;
}
.user .voiceBtn {
  width: 12vw;
  height: 15vw;
  padding: 0px;
}
.user .voiceBtn img {
  width: auto;
  height: 9vw;
  margin: 3vw auto;
}
.user .content {
  width: 65vw;
  height: 15vw;
}
.user .content textarea {
  width: 100%;
  box-sizing: border-box;
  height: 12vw;
  margin-top: 1.5vw;
  border: 1.5px solid #ccc;
  border-radius: 10px;
  outline: none;
  resize: none;
  padding: 5px;
  font-size: 5vw;
}
.user .BTNBOX {
  width: 22vw;
  height: 15vw;
}
.user .BTNBOX img {
  float: left;
  width: 8vw;
  height: auto;
  margin-top: 3.5vw;
  margin-left: 0.6vw;
}
.user .BTNBOX button {
  width: 12vw;
  height: 7vw;
  margin: 4vw 0.5vw;
  font-size: 3.5vw;
}

.userContent {
  height: 100%;
  border-top: 1px solid #999;
  display: flex;
  justify-content: space-between;
}
#sendText {
  width: 80%;
}
.send {
  width: 20%;
}

.chat {
  overflow: hidden;
  overflow-y: auto;
  margin-top: 10vw;
  padding-bottom: 15.5vw;
  box-sizing: border-box;
  height: 100vh;
}
.list {
  padding: 10px 0;
  flex-wrap: nowrap;
}
.head {
  width: 12vw;
  height: auto;
  border-radius: 50%;
  overflow: hidden;
}
.head img {
  width: 100%;
  height: 100%;
}
.msgText {
  font-size: 4vw;
  padding: 1.5vw 2.5vw;
  max-width: 60%;
  background-color: #fff;
  border-radius: 2vw;
  position: relative;
  margin-left: 3vw;
  line-height: 5vw;
}
.msgText p {
  padding-top: 5px;
}
.boo {
  position: absolute;
  width: 0;
  height: 0;
  border-top: 5px solid transparent;
  border-right: 10px solid #fff;
  border-bottom: 5px solid transparent;
  border-left: 10px solid transparent;
  top: 30%;
  left: -20px;
}
.myList {
  flex-direction: row-reverse;
}
.myList .msgText {
  margin-left: 0;
  margin-right: 20px;
  background-color: #57d6dd;
  color: #fff;
}
.myList .myboo {
  position: absolute;
  width: 0;
  height: 0;
  border-top: 5px solid transparent;
  border-right: 10px solid transparent;
  border-bottom: 5px solid transparent;
  border-left: 10px solid #57d6dd;
  top: 30%;
  right: -20px;
}
</style>


