<template>
    <div>
        <mt-header fixed="true" style="height:12vw;background:white !important;">
            <router-link to="/friend" slot="left">
                <mt-button icon="back" style="color:#666;"></mt-button>
            </router-link>
            <div slot="right" class="textbox">
                <input type="text" v-model="user" placeholder="请输入好友手机号｜账号｜昵称">
            </div>
        </mt-header>
        <div class="resultbox">
            <div class="resultTitle">搜索结果</div>
            <div class="resultList">
                <div>
                    <div ></div>
                    <div></div>
                    <div></div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
console.log("USER_PAGEFRIEND_ADDFRIEND_VUE");
// import root from "@/config/root.js";
export default {
  data() {
    return {
      user: "",
      title: "<input type='text'>",
      searchType: "",
      inputOn: false
    };
  },
  methods: {
    searchFriend: function() {
    
    }
  },
  watch: {
    searchType: function() {
      if (this.searchType == "id") {
        this.inputOn = false;
      } else {
        this.inputOn = true;
      }
    }
  },

  beforeCreate: function() {
    //   判断是否登录
    if (this.GLOBAL.userInfo.loginStatus) {
        // 判断websocket是否建立
      if (!this.GLOBAL.socketHand) {
        this.createSocket();
      }
      document.getElementsByTagName("body")[0].className = "bgc-fff";
    } else {
      this.$router.push("/login");
    }
  },
  beforeDestroy: function() {
    document.body.removeAttribute("class", "bgc-fff");
  }
};
</script>

<style scoped>
.textbox{
    position: absolute;
    top: 0px;
    left: 7.5vw;
    width: 85vw;
    border-bottom: 1px solid #00FFFF;
    height: 12vw;
}
.textbox input{
    width: 100%;
    height: 100%;
    border: 0px;
    outline: none;
    font-size: 4vw;
    color: #666;
}

</style>


