<template>
    <div>
        <mt-header title="我的好友" fixed="true" style="height:10vw;font-size:5vw;">
            <router-link to="/user" slot="left">
                <mt-button icon="back">返回</mt-button>
            </router-link>
            <router-link to="/addFriend" slot="right">
                <mt-button>
                    <i class="iconfont icon-tianjiahaoyou" style="font-size:20px"></i>
                </mt-button>
            </router-link>
        </mt-header>

        <div class="module">
            <div style=" margin-top: 10vw;" class="f-title" @click="friendSlide">全部好友 ({{friendOnline}}/{{friends.length?friends.length:0}})
                <i class="iconfont" :class="friendsShow ? iconDown : iconUp"></i>
            </div>
            <div v-for="(value,index) in friends" v-bind:key="index" v-show="friendsShow">
                <div class="f-list" v-bind="onlineCompulated(value.on_status,'friend')">
                    <router-link class="f-info" :to="{path: '/chat', query: {fid: value.id, nickname: value.nickname, head:value.head, status: 1}}">
                        <div class="f-info">
                            <div class="f-pic">
                                <img v-bind:src="host + value.head">
                                <mt-badge type="error" size="small" class="badge friend-badge" v-show="msgList[value.id]">{{msgList[value.id]}}</mt-badge>
                            </div>
                            <div class="f-main">
                                <div class="f-username">{{value.nickname}}
                                    <span v-if="value.on_status">[在线]</span>
                                    <span v-else>[离线]</span>
                                </div>
                                <!-- <div v-if="msgList[value.id]" class="f-medal">
                                    {{msgList[value.id][Object.keys(msgList[value.id]).length-1].content}}
                                </div> -->
                                <div class="f-medal" v-for="(medal,mIndex) in value.medal" v-bind:key="mIndex">
                                    <img v-bind:src="host + medal">
                                </div>
                            </div>
                        </div>
                    </router-link>
                    <div class="f-btn">
                        <button class="del" @click="friendDel(value,index)">删除好友</button>
                    </div>
                </div>
            </div>
        </div>
        <div class="module">
            <div class="f-title" @click="strangerSlide">陌生人({{strangeOnline}}/{{strange.length?strange.length:0}})
                <i class="iconfont" :class="strangeShow ? iconDown : iconUp"></i>
            </div>
            <div v-for="(value,index) in strange" v-bind:key="index" v-show="strangeShow">
                <div class="f-list" v-bind="onlineCompulated(value.on_status,'strange')">
                    <router-link class="f-info" :to="{path: '/chat', query: {fid: value.id, nickname: value.nickname, head: value.head, status: 2}}">
                        <div class="f-info">
                            <div class="f-pic">
                                <img v-bind:src="host+value.head">
                                <mt-badge type="error" size="small" class="badge friend-badge" v-show="msgList[value.id]">{{msgList[value.id]}}</mt-badge>
                            </div>
                            <div class="f-main">
                                <div class="f-username">{{value.nickname}}
                                    <span v-if="value.on_status">[在线]</span>
                                    <span v-else>[离线]</span>
                                </div>
                                <!-- <div v-if="msgList[value.id]" class="f-medal">
                                {{msgList[value.id][Object.keys(msgList[value.id]).length-1].content}}
                            </div> -->
                                <div class="f-medal" v-for="(medal,mIndex) in value.medal" v-bind:key="mIndex">
                                    <img v-bind:src="host + medal">
                                </div>
                            </div>
                        </div>
                    </router-link>
                    <div class="f-btn">
                        <button class="del">删除</button>
                    </div>
                </div>
            </div>
        </div>

        <div class="module">
            <div class="f-title" @click="systemSlide">系统消息({{system.length?system.length:0}})
                <i class="iconfont" :class="systemShow ? iconDown : iconUp"></i>
                <mt-badge type="error" size="small" class="badge friend-badge" v-show="msgList['system']">{{msgList['system']}}</mt-badge>
            </div>
            <div v-for="(value,index) in system" :key="index" v-show="systemShow">
                <div class="f-list">
                    <!-- <router-link  class="f-info" :to="{path: '/chat', query:{fid: value.id, nickname: value.nickname, head: value.head}}"> -->
                    <div class="f-info">
                        <div class="f-pic">
                            <img v-bind:src="host+value.head">
                        </div>
                        <div class="f-main">
                            <div class="f-username">{{value.nickname}}
                                <span v-if="value.on_status">[在线]</span>
                                <span v-else>[离线]</span>
                            </div>
                            <div class="f-medal">
                                {{value.content}}
                            </div>
                            <!-- <div v-else class="f-medal" v-for="(medal,mIndex) in value.medal" v-bind:key="mIndex">
                                <img v-bind:src="host + medal">
                            </div> -->
                        </div>
                    </div>
                    <!-- </router-link> -->
                    <div v-if="value.status==4" class="f-btn">
                        <button class="accept" @click="agreen(value,index)">接受</button>
                        <button class="del" @click="refuse(value,index)">拒绝</button>
                    </div>
                    <div v-else class="f-btn">
                        <button class="del">删除</button>
                    </div>

                </div>
            </div>
        </div>
    </div>
</template>

<script>
console.log("USER_PAGEFRIEND_FRIEND_VUE");
import pic1 from "./../../../assets/pic1.png";
import root from "@/config/root.js";
export default {
  data() {
    return {
      friends: this.GLOBAL.friendLists.friends,
      strange: this.GLOBAL.friendLists.strange,
      system: this.GLOBAL.friendLists.system,
      friendsShow: true,
      strangeShow: false,
      systemShow: false,
      host: this.GLOBAL.Host,
      iconDown: "icon-jiantouxia",
      iconUp: "icon-jiantoushang",
      friendOnline: 0,
      strangeOnline: 0,
      msgList: this.GLOBAL.friendMsgNum
    };
  },
  methods: {
    onlineCompulated: function(status, flag) {
      if (flag == "friend") {
        this.friendOnline++;
      } else {
        this.strangeOnline++;
      }
    },
    friendSlide: function() {
      console.log("friendslide");
      this.friendsShow = !this.friendsShow;
    },
    strangerSlide: function() {
      this.strangeShow = !this.strangeShow;
    },
    systemSlide: function() {
      this.systemShow = !this.systemShow;
    },
    friendDel: function(value, index) {
      this.$messagebox
        .confirm("确定要删除好友" + value.nickname + "吗？")
        .then(action => {
          this.$axios
            .post(root.friend, {
              id: value.id
            })
            .then(response => {
              // this.$store.state.songInfo.friends = response.data;
            });
        });
    },
    agreen: function(value, index) {
      this.$axios
        .post(root.strange, {
          id: value.id,
          code: 1
        })
        .then(response => {
          console.log(response);
          // this.$store.state.songInfo.friends = response.data.friends;
          // console.log(this.$store.state.songInfo.addfriends)
          // this.$store.state.songInfo.addfriends.splice(index);
        });
    },
    refuse: function(value, index) {
      console.log(value);
      this.$axios
        .post(root.strange, {
          id: value.id,
          code: 0
        })
        .then(response => {
          if (response.data.code) {
            this.$messagebox.alert("拒绝添加好友成功").then(action => {
              // this.$store.state.songInfo.addfriends.splice(index);
            });
          }
        });
    },
    sendAdd: function(value, index) {
      this.$axios
        .post(root.sendAdd, {
          id: value.id
        })
        .then(response => {
          console.log(response);
          // if(value.id == this.$store.state.songInfo.user.id){
          // this.$messagebox.alert('不能添加自己为好友！')
          // }
          if (response.data.code == 2) {
            this.$messagebox.alert("已经是好友了！");
          }
          if (response.data.code == 0) {
            this.$messagebox.alert("发送添加好友请求失败！");
          }
          if (response.data.code == 1) {
            this.$messagebox.alert("发送添加好友请求成功！");
          }
          if (response.data.code == 3) {
            this.$messagebox.alert("请不要多次点击添加！");
          }
          if (response.data.code == 4) {
            this.$messagebox.alert("添加好友成功！");
          }
        });
    }
  },
  created: function() {},
  beforeCreate: function() {
    //   判断是否登陆
    if (this.GLOBAL.userInfo.loginStatus) {
      // 判断是否创建socket
      if (!this.GLOBAL.socketHand) {
        this.createSocket(function(that) {
          localStorage.setItem("bc_global", JSON.stringify(that.GLOBAL));
        });
      }
    } else {
      this.$router.push("/login");
    }
    this.$parent.tabbarShow = true;
    this.$parent.tabbarWhic = false;
  }
};
</script>

<style scoped>
a {
  display: block;
}
.module {
  padding: 0 10px;
  margin: 10px 0;
  background-color: #fff;
}
.f-title {
  text-align: left;
  height: 12vw;
  line-height: 14vw;
  font-size: 4.5vw;
}
.f-title .iconfont {
  font-size: 4vw;
  color: #999;
}
.f-list {
  border-top: 1px solid #e7e7e7;
  height: 12vw;
  /* line-height: 45px; */
  padding: 10px 0;
  display: flex;
  justify-content: space-between;
}
.friend-badge {
  display: block;
  width: 16px;
  height: 16px;
  font-size: 0.75rem;
  padding: 1px 1px;
  line-height: normal;
  position: absolute;
  left: 30px;
  top: -5px;
}
.f-pic {
  width: 12vw;
  height: 12vw;
  border-radius: 12vw;
  overflow: hidden;
  margin-right: 10px;
}
.f-pic img {
  width: 100%;
  height: 100%;
}
.f-info {
  display: block;
  width: 80%;
  display: flex;
  flex-wrap: nowrap;
  position: relative;
}
.f-medal {
  display: flex;
  flex-wrap: nowrap;
  font-size: 12px;
  color: #999;
  line-height: 20px;
}
.f-medal img {
  width: 15px;
  height: 20px;
}
.f-username {
  /* height: 25px; */
  line-height: 5vw;
  font-size: 3vw;
  text-align: left;
}
.f-username span {
  font-size: 2.5vw;
  color: #64d7e3;
}
.f-btn {
  line-height: 45px;
  height: 45px;
  width: 40%;
  text-align: end;
}
.del {
  background-color: #e7e7e7;
  color: #999;
  font-size: 2.5vw;
  border: none;
  /* height: 30px; */
  line-height: 8vw;
  padding: 0 15px;
}
.accept {
  background-color: #64d7e3;
  color: #fff;
  font-size: 12px;
  border: none;
  height: 30px;
  line-height: 30px;
  padding: 0 15px;
}
</style>


